/*首页*/
body{
}
.desktopCloud-bg{
    /*background: url('../assets/desktopCloud-bg.jpg') no-repeat;*/
    width: 100%;
    position: absolute;
    z-index: -1;
}
.desktopCloud-bottom{
    background: url('../assets/bottomBar.png') repeat-x;
    height:48px;
    position:fixed;
    bottom: 0;
    width: 100%;
}
.desktopCloud-content{
    width:715px;
    padding: 30px 20px;
    margin: 0 auto;
        box-shadow:0px 0px 20px -5px #000;
}
.desktopCloudBar-hd{
    width: 715px;
    height:184px;
    /*background: url('../assets/campus-broadcastBar.png') no-repeat;*/
    box-shadow:0px 0px 20px -5px #000;
}
.desktopCloudBar-hd-icon{
    background: url('../assets/campus-broadcast-tips.png') 30px 0 no-repeat;
    width: 60px;
    height: 60px;
    line-height: 60px;
    font-size: 25px;
    padding-left: 110px;
}
.desktopCloudBar-hd-title{
    font-size: 27px;
    padding-left: 47px;
    margin-top: 8px;
}
.desktopCloudBar-hd-content{
    padding-left: 47px;
    margin-top: 12px;
    line-height: 24px;
}
.desktopCloudBar-bd{
    margin-top: 10px;
}
.desktopCloudBar-l{
    background: url('../assets/courseBar.png') no-repeat;
